.navbar-fixed-bottom {
	background-image: -webkit-gradient(linear,  left top, left bottom,  from(rgba(173, 52, 62, 1)), to(rgba(224, 133, 14, 1)));
	background-image: -webkit-linear-gradient(top,  rgba(173, 52, 62, 1), rgba(224, 133, 14, 1));
	background-image: -o-linear-gradient(top,  rgba(173, 52, 62, 1), rgba(224, 133, 14, 1));
	background-image: linear-gradient(180deg,  rgba(173, 52, 62, 1), rgba(224, 133, 14, 1));
	color: rgba(255,255,255,.6);
	padding: 20px;
}


