* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

body {
  background-image: url('./images/matrixgridfloor.png');
}

hr{
  opacity:0;
}

button {
  padding: 0;
}

a:hover {
  text-decoration: none;
}

a {
  color: inherit;
  text-decoration: none;
}

.jumbotron {
  border: 2px solid white;
  margin: 0;
}

.bungeeFont {
  font-family: 'Bungee Inline', cursive;
}

.monotonFont {
  font-family: 'Monoton', cursive;
}

.rainbow {
  background: -webkit-gradient(linear, right top, left top, from(#0f8a6b), color-stop(#28a70c), color-stop(#cad70d), color-stop(#d73b0d), to(#d70dc6));
  background: -webkit-linear-gradient(right, #0f8a6b, #28a70c, #cad70d, #d73b0d, #d70dc6);
  background: -o-linear-gradient(right, #0f8a6b, #28a70c, #cad70d, #d73b0d, #d70dc6);
  background: linear-gradient(270deg, #0f8a6b, #28a70c, #cad70d, #d73b0d, #d70dc6);
  background-size: 1000% 1000%;
  -webkit-animation: AnimationName 11s ease infinite;
          animation: AnimationName 11s ease infinite; 
}

@-webkit-keyframes AnimationName {
  0% {
      background-position: 96% 0%
  }
  50% {
      background-position: 5% 100%
  }
  100% {
      background-position: 96% 0%
  }
}

@keyframes AnimationName {
  0% {
      background-position: 96% 0%
  }
  50% {
      background-position: 5% 100%
  }
  100% {
      background-position: 96% 0%
  }
}

.panel {
  background-color: rgba(250,250,250,.3);
  margin-top: 10px;
}


