header {
    color: white;
    background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(224, 133, 14, 1)), to(rgba(173, 52, 62, 1)));
    background-image: -webkit-linear-gradient(top, rgba(224, 133, 14, 1), rgba(173, 52, 62, 1));
    background-image: -o-linear-gradient(top, rgba(224, 133, 14, 1), rgba(173, 52, 62, 1));
    background-image: linear-gradient(180deg, rgba(224, 133, 14, 1), rgba(173, 52, 62, 1));
}

.container {
    width: 100%;
}






